<template>
  <h-page-container class="home">
    <h-page-header :breadcrumb="breadcrumb"></h-page-header>
    <h-page-content class="home-content">
      <h-page-search ref="searchForm" :model="searchForm">
        <h-page-search-item prop="contactName" label="联系人">
          <el-input
            v-model.trim="searchForm.contactName"
            :maxlength="32"
            placeholder="请输入联系人"
            clearable
          ></el-input>
        </h-page-search-item>
        <h-page-search-item prop="contactPhone" label="手机号码">
          <el-input
            v-model.trim="searchForm.contactPhone"
            :maxlength="13"
            placeholder="请输入手机号码"
            clearable
          ></el-input>
        </h-page-search-item>
        <h-page-search-item prop="carNum" label="车牌号">
          <el-input
            v-model.trim="searchForm.carNum"
            :maxlength="13"
            placeholder="请输入车牌号"
            clearable
          ></el-input>
        </h-page-search-item>
        <h-page-search-item prop="alarmTime" label="报警时间">
          <el-date-picker
            v-model="searchForm.alarmTime"
            type="datetimerange"
            start-placeholder="开始时间"
            end-placeholder="结束时间"
          />
        </h-page-search-item>
        <h-page-search-item prop="notifyStatus" label="短信通知状态">
          <el-select
            v-model="searchForm.notifyStatus"
            placeholder="请选择短信通知状态"
          >
            <el-option
              v-for="item in notifyStatusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </h-page-search-item>
        <template slot="pageSearchAction">
          <el-button type="primary" @click="query">搜索</el-button>
          <el-button @click="reset">重置</el-button>
        </template>
      </h-page-search>
      <h-page-table>
        <el-table :data="tableData" show-overflow-tooltip stripe>
          <el-table-column
            prop="alarmName"
            label="报警名称"
            show-overflow-title
          ></el-table-column>
          <el-table-column
            prop="sensorName"
            label="通道名称"
            show-overflow-title
          ></el-table-column>
          <el-table-column
            prop="alarmTime"
            label="报警时间"
            show-overflow-title
          ></el-table-column>
          <el-table-column
            prop="alarmType"
            label="报警类型"
            show-overflow-title
          ></el-table-column>
          <el-table-column
            prop="contactName"
            label="联系人"
            show-overflow-title
          ></el-table-column>
          <el-table-column
            prop="contactPhone"
            label="手机号码"
            show-overflow-title
          ></el-table-column>
          <el-table-column
            prop="carNum"
            label="车牌号"
            show-overflow-title
          ></el-table-column>
          <el-table-column
            prop="alarmPosition"
            label="报警地点"
            show-overflow-title
          ></el-table-column>
          <el-table-column label="短信通知状态" show-overflow-title>
            <template slot-scope="scope">
              <span class="green-style" v-if="scope.row.notifyStatus === 1"
                >已通知</span
              >
              <span
                class="orange-style"
                v-else-if="scope.row.notifyStatus === 2"
                >未通知</span
              >
              <span class="red-style" v-else-if="scope.row.notifyStatus === 3"
                >通知失败</span
              >
            </template>
          </el-table-column>
          <el-table-column
            prop="notifyTime"
            label="通知时间"
            show-overflow-title
          ></el-table-column>
          <el-table-column
            prop="remark"
            label="备注"
            show-overflow-title
          ></el-table-column>
        </el-table>
        <el-pagination
          slot="pagination"
          @size-change="sizeChange"
          @current-change="currentChange"
          :current-page="page"
          :page-sizes="pageSizes"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </h-page-table>
    </h-page-content>
  </h-page-container>
</template>

<script>
import { getTableData } from "../api/homeApi";
import moment from "moment";

export default {
  name: "Home",
  data() {
    return {
      breadcrumb: ["公安", "短信通知详情"],
      searchForm: {
        contactName: "",
        contactPhone: "",
        carNum: "",
        alarmTime: [
          new Date(new Date().getTime() - 3600 * 1000 * 24 * 30),
          new Date(),
        ],
        notifyStatus: "",
      },
      notifyStatusOptions: [
        {
          label: "全部",
          value: "",
        },
        {
          label: "已通知",
          value: 1,
        },
        {
          label: "未通知",
          value: 2,
        },
        {
          label: "通知失败",
          value: 3,
        },
      ],
      tableLoading: false,
      tableData: [], // 表格数据
      page: 1, // 当前页
      pageSize: 20, // 一页容量
      total: 0, // 总条数
      isCreatedGetTable: true, // 是否在created 加载列表
      pageSizes: [10, 20, 50, 100],
    };
  },
  methods: {
    reset() {
      this.$refs.searchForm.resetFields();
      this.page = 1;
      this._getTableData();
    },
    query() {
      this.page = 1;
      this._getTableData();
    },
    sizeChange(size) {
      this.pageSize = size;
      this.query();
    },
    currentChange(page) {
      this.page = page;
      this._getTableData();
    },
    selectionChange(val) {
      this.multipleSelection = val;
    },
    _initTable() {
      this.total = 0;
      this.page = 1;
      this.tableData = [];
    },
    async _getTableData() {
      try {
        const params = {
          pageNo: this.page,
          pageSize: this.pageSize,
          contactName: this.searchForm.contactName,
          contactPhone: this.searchForm.contactPhone,
          carNum: this.searchForm.carNum,
          startTime: this.searchForm.alarmTime.length
            ? moment(this.searchForm.alarmTime[0]).format("YYYY-MM-DD HH:mm:ss")
            : "",
          endTime: this.searchForm.alarmTime.length
            ? moment(this.searchForm.alarmTime[1]).format("YYYY-MM-DD HH:mm:ss")
            : "",
          notifyStatus: this.searchForm.notifyStatus,
        };
        let { code, data } = await getTableData(params);
        if (code === "0") {
          if (data && data.list) {
            this.tableData = data.list || [];
            this.page = data.pageNo;
            this.total = data.total;
          } else {
            this.tableData = [];
            this.page = 1;
            this.total = 0;
          }
        }
      } catch (error) {
        console.log(error);
      }
    },
  },
  mounted() {
    this._getTableData();
  },
};
</script>

<style lang="scss">
.home {
  height: 100%;
  .green-style {
    color: #0f0;
  }
  .orange-style {
    color: rgb(255, 123, 0);
  }
  .red-style {
    color: #f00;
  }
}
</style>
