import axios from 'axios'
import {
    Message
} from 'hui'

const http = axios.create({
    timeout: 60000, // 指定请求超时的毫秒数
    withCredentials: true, // 表示跨域请求时是否需要使用凭证
    headers: {
        'Content-Type': 'application/json',
        'X-Requested-With': 'XMLHttpRequest'
    } // 即将被发送的自定义请求头
})

let timer = null

// 请求拦截器
http.interceptors.request.use(config => {
    let {
        method,
        params
    } = config
    if (method === 'get') {
        const timestamp = new Date().getTime()
        if (params) {
            params.__timestamp = timestamp
        }
    }
    return config
}, error => {
    return Promise.reject(error)
})

// 响应拦截器
http.interceptors.response.use(response => {
    if (response.data.code !== '0') {
        if (!response.config.noMsg && response.data.msg) {
            Message.error('服务异常')
        }
        return Promise.reject(response)
    }

    return Promise.resolve({
        code: response.data.code,
        msg: response.data.msg,
        data: response.data.data
    })
}, error => {
    timer && window.clearTimeout(timer)
    timer = window.setTimeout(() => {
        proxyErrMsg(error)
        timer = null
    }, 100)
    return Promise.reject(error)
})

function proxyErrMsg(err) {
    const {
        message
    } = err
    // 超时
    if (message.indexOf('timeout') > -1) {
        Message.error('请求超时，请重试！')
    }
    // 404
    if (message.indexOf('404') > -1) {
        Message.error('连接不上远程服务器,请检查网络！')
    }
    // 网络断开
    if (message.indexOf('Network Error') > -1) {
        Message.error('网络断开,请检查网络')
    }
}

export default http