import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import HUI from 'hui'
import 'hui/lib/hui.css'
import locale from 'hui/lib/locale/lang/zh-CN'
import './huiPro'

Vue.use(HUI, {
  locale
});

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
